.header {
	background-color: rgb(255, 255, 255);
	min-height: 210px;
	color: $darkFonts;
}

.header__firstLine {
	display: flex;
	justify-content: flex-end;
	min-height: 112px;
	position: relative;
	align-items: center;

	&::after {
		content: "";
		position: absolute;
		width: 106%;
		height: 1px;
		border-bottom: 1px solid #b7b7b7;
		bottom: 0;
		left: -3%;
	}
}

.header__linklogo {
	margin-right: auto;
}
.linklogo__logo {
	width: 242px;
	height: 70px;
	fill: $basicColor;
	transition: fill 0.8s;
}
.linklogo {
	display: block;
	line-height: 0;
	&:hover .linklogo__logo {
		fill: $basicColor * 1.1;
		transition: fill 0.3s;
	}
}

// .header__secondLine {
// 	min-height: 97px;
// }

.nav {
	// min-height : 97px;
	font-weight: 600;
	color: $lightFonts;
	font-size: 15px;
}

.nav__list {
	display: flex;
	justify-content: flex-start;
	align-content: center;
}

.nav__item {
	display: block;
	padding: 33px 10px;
	text-decoration: none;
	text-transform: uppercase;
	color: inherit;
	transition: transform 0.5s;

	&:hover {
		transform: scale(1.1);
		transition: transform 0.15s;
	}
}

body[data-page="home"] [data-item="home"],
body[data-page="about"] [data-item="about"],
body[data-page="services"] [data-item="services"],
body[data-page="expert"] [data-item="expert"],
body[data-page="gallery"] [data-item="gallery"],
body[data-page="contacts"] [data-item="contacts"] {
	color: $darkFonts;
	font-weight: 800;
}

.nav__item-right {
	margin-left: auto;
	width: 250px;
	align-self: center;
	padding-left: 15px;
}

.nav__btn {
	font-size: inherit;
	cursor: pointer;
	width: 100%;
}

.contact {
	font-family: "OpenSans";
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 14px;
	font-weight: 600;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.43;
	letter-spacing: normal;
	text-align: left;
	color: $darkFonts;
	width: fit-content;
	// transition: color 0.5s;
}

.contact__address {
	padding-left: 39px;
}
.contact__address > span,
.contact__schedule > span,
contact__phone > span {
	display: block;
}

.phone__list {
	&:hover ~ .icon-phone-cont:before {
		color: $basicColor;
	}
}

.icon-position-cont:before,
.icon-phone-cont:before,
.icon-clock-cont:before {
	color: $secondaryColor;
	position: absolute;
	left: 0;
	top: calc(50% - 17px);
	font-size: 35px;
	font-weight: 400;
	transition: color 0.8s;
}

.icon-position:before {
	content: "\e900";
}

.icon-phone:before {
	content: "\e901";
}

.icon-clock:before {
	content: "\e902";
}
@media screen and (max-width: 950px) {
	.header {
		min-height: 175px;
	}
	// .header__linklogo {
	// 	width: 180px;
	// 	height: 53px;
	// }
	.linklogo__logo {
		width: 180px;
	}

	.contact {
		font-size: 12px;
	}

	.icon-position-cont:before,
	.icon-phone-cont:before,
	.icon-clock-cont:before {
		font-size: 24px;
		top: calc(50% - 12px);
	}

	.contact__phone,
	.contact__schedule,
	.contact__address {
		color: $darkFonts;
		padding-left: 35px;
		margin-left: 4%;
	}

	.nav {
		font-size: 12px;
		// visibility: hidden;
	}

	.nav__item {
		padding: 21px 9px;
	}
}

#menu__toggle,
.menu__btn {
	display: none;
}
.nav__item-footer {
	display: none;
	// visibility: hidden;
}
@media screen and (max-width: 800px) {
	.header {
		min-height: 144px;
	}
	.contact {
		height: 42px;
	}
	.header .container {
		padding: initial;
	}
	.header__firstLine {
		flex-direction: column-reverse;
		align-items: center;
	}
	.header__linklogo {
		width: 155px;
		margin: 15px auto auto;
	}
	.header__contact {
		justify-content: center;
		width: 100%;
		background-color: $basicColor;
		// color: #ffdbe9;
	}
	.contact__phone,
	.contact__schedule {
		color: #ffdbe9;
		// & :hover{
		// 	color: #fff;
		// }
	}
	.phone__call:hover {
		color: #fff;
	}
	// .contact__schedule > span:nth-child(2) {
	// 	display: none;
	// }
	// .icon-position-cont:before, .icon-phone-cont:before, .icon-clock-cont:before{
	// 	fill
	// }
	.icon-phone:before {
		color: #ffdbe9;
	}
	.icon-clock:before {
		color: #ffdbe9;
	}
	.phone__list:hover ~ .icon-phone-cont:before {
		color: white;
	}
	.header__firstLine:after {
		border: none;
	}
	.header__nav {
		position: relative;
		right: 120vw;
		z-index: 100;
		overflow: hidden;
		transition: right 0.5s;
	}
	.header__secondLine {
		width: 100%;
		position: absolute;
		height: 100px;
		top: 35px;
	}
	.nav__list {
		flex-direction: column;
		font-size: 20px;
		line-height: 24px;
		background-color: #fffffff2;
		position: relative;
		height: 100%;
		& > li {
			margin-left: 36px;
		}
		& li:last-child {
			margin-left: 0;
		}
	}
	.nav__btn{
		height: 42px;
	}
	.nav__item {
		&:hover {
			transform: scale(1.1) translateX(33px);
			transition: 0.3s ease-in-out;
		}
	}
	.nav__item-right {
		margin-left: initial;
		align-self: initial;
		font-size: 13px;
		margin-top: 22px;
		margin-bottom: 98px;
	}
	.nav__btn {
		height: 40px;
		width: 190px;
	}
	.nav__item-footer {
		display: inherit;
		position: absolute;
		right: 0;
		left: 0;
		bottom: 0;
		height: 48px;
		background-color: $basicColor;
		margin-left: 0;
		& .social__list {
			top: initial;
			margin-left: 35px;
		}
		& .social__icon {
			margin-top: 7px;
		}
	}
	@mixin trasit {
		transition: transform 0.3s cubic-bezier(0.4, 0, 1, 1), width 0.3s ease-out, top 0.3s linear, background-color 1s;
	}
	.menu__btn {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 60px;
		height: 60px;
		cursor: pointer;
		z-index: 1;
		margin-left: 5vw;
		margin-top: 27px;
		& span,
		& span::before,
		& span::after {
			display: block;
			margin: auto;
			position: absolute;
			width: 40px;
			height: 4px;
			border-radius: 50px;
			background-color: #d5d5d5;
			@include trasit;
		}
		& span::before {
			content: "";
			top: -10px;
			width: 50px;
		}
		& span::after {
			content: "";
			top: 10px;
			width: 30px;
		}
		&:hover {
			& span,
			& span:after,
			& span:before {
				background-color: #c0c0c0;
			}
		}
	}

	.menu__btn.active {
		& ~ .header__nav {
			visibility: visible;
			right: 0;
			transition: right 0.5s;
		}
		& span {
			height: 0px;
			background-color: rgba(255, 255, 255, 0);
			transition: background-color 0.2s cubic-bezier(0.42, 0, 0.32, 1.01), height 0.5s, width 1.2s;
		}

		& span::before {
			transform: rotate(45deg);
			width: 40px;
			top: 0;
			@include trasit;
		}
		& span::after {
			transform: rotate(-45deg);
			width: 40px;
			top: 0;
			@include trasit;
		}
	}

	.social__list li {
		margin-right: 6px;
	}

	.menu__toggle {
		opacity: 0;
	}

	.contact__address {
		display: none;
		visibility: hidden;
	}
}
