/*----- Popupform -----*/
.appointment {
	width: 850px;
	// margin-right: 30px;
	// margin-left: 30px;
	min-height: 740px;
	// padding-bottom: 43.75%;
}

.appointment,
.appointment__form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	user-select: none;
	padding-bottom: inherit;
}
.appointment__cont {
	width: 448px;
	text-align: center;
}
.appointment__cont * {
	width: 100%;
}
.appointment__head {
	font-family: "Montserrat";
	font-size: 36px;
	color: $basicColor;
	margin-top: 84px;
	margin-bottom: 60px;
}
.appointment__appeal {
	font-family: "OpenSans";
	font-size: 20px;
	color: $darkFonts;
}
// .form * {
// 	margin-bottom: 38px;
// }
.form {
	position: relative;
}
.select__header,
.form__date,
.form__name,
.form__tel {
	height: 60px;
	border-radius: 30px;
	border: solid 1px #000;
	font-family: "Open Sans";
	font-size: 20px;
	font-weight: bold;
	// text-align: center;
	color: #707070;
	padding-left: 31px;
	cursor: pointer;
	outline: none;
	margin-bottom: 38px;

	&:focus, &:hover {
		border-color: $basicColor;
	}
}

.form__select {
	position: relative;
	height: 60px;
	margin-bottom: 38px;
}
.form__select select {
	display: none;
}

.form__exp {
	padding-right: 20px;
	appearance: none;
	position: relative;
}

.form__select select {
	display: none;
}
.select__header {
	display: flex;
	text-align: start;
	transition: border-radius 0.1s;
	margin-bottom: 0px;

	&:hover {
		border-color: $basicColor;
	}
	&:hover .select__cont {
		// height: auto;
		display: inherit;
	}
	//убрать  active
	&-active {
		border-radius: 30px 30px 0 0;
		border-color: $basicColor;
	}
	&-active .select__cont {
		height: auto;
		display: inherit;
	}
	&-active .select__body {
		border-color: $basicColor;
		top: 0px;
	}
	&:before {
		content: "";
		display: block;
		position: absolute;
		top: 50%;
		right: 20px;
		margin-top: -3px;
		border-style: solid;
		border-width: 10px 10.5px 0 10.5px;
		border-color: $basicColor transparent transparent transparent;
	}
}
.select__cont {
	overflow: hidden;
	position: absolute;
	width: 100%;
	height: 0;
	display: none;
	top: 59px;
	left: 0;
}
.select__curent {
	margin: auto;
	pointer-events: none;
}
.select__body {
	width: 100%;
	border: solid 1px #000;
	border-top: none;
	background-color: #fff;
	border-radius: 0 0 30px 30px;
	position: relative;
	top: -500px;
	transition: height 0.3s, opacity 0.2s, top 0.3s;

	// &:hover ~ .select__header {
	// 	border-radius: 30px 30px 0 0;
	// 	border-color: $basicColor;
	// }
	&:hover {
		border-color: $basicColor;
	}
}

.select__item {
	height: 60px;
	display: flex;
	justify-content: start;
	align-items: center;
	font-family: "Open Sans";
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 0;
	color: #707070 * 1.5;
	padding-left: 31px;
	cursor: pointer;
	border-top: solid 1px #000;
	transition: color 0.3s;
	&:hover {
		color: #707070;
	}
}
.form__date {
	appearance: none;
	// ::-webkit-inner-spin-button { display: none; }
	// ::-webkit-calendar-picker-indicator { display: none; }
}
.form {
	& .datepicker-inline {
		position: relative;
		bottom: 35px;
		// width: 100%;
	}
	& .datepicker.my-datepicker {
		position: absolute;
		// width: 100%;
		border-radius: 19px;
		opacity: 0;
		// display: none;
		width: 0;
		height: 0;
		overflow: hidden;
		transition: opacity 0.3s;
		transition-delay: display 1s, background-color 1s;
		
	}
	& .datepicker.my-datepicker-active {
		opacity: 1;
		display: block;
		// transition-delay: display .5s;
		transition: opacity 0.3s;
		width: 100%;
		height: auto;
	}

	.datepicker--nav-action path {
		stroke: $basicColor;
	}
	.datepicker--day-name {
		color: $basicColor;
	}
	.datepicker--cell.-current- {
		color: $basicColor;
	}
	.datepicker--cell.-focus- {
		background: rgba(236, 64, 121, 0.226);
		// background-color: transparentize($basicColor, 0.2);
		// background-color: transparentize($translucent-red, 0.25);
	}
	.datepicker--content {
		position: relative;
		right: 4px;
	}
	.datepicker--cell.-selected- {
		background-color: $basicColor;
	}
	.datepicker--cell.-selected-.-focus- {
		background-color: $basicColor * 0.95;
	}
	.datepicker--nav-title {
		font-family: inherit;
		font-size: 15px;
		font-weight: bold;
		color: $basicColor;
		& i {
			color: $lightFonts;
			font-family: inherit;
			font-size: 15px;
			font-weight: bold;
		}
	}
	.datepicker--button {
		color: $lightFonts;
		font-family: inherit;
		font-size: 15px;
		font-weight: bold;
	}
	.datepicker--button,
	.datepicker--nav-title {
		border-radius: 16px;
	}
	.datepicker--button:hover,
	.datepicker--nav-title:hover {
		background-color: rgba(236, 64, 121, 0.226);
	}
}

.form__placeholder {
	// font-family: 'Open Sans';
	position: absolute;
	font-size: 20px;
	font-weight: bold;
	text-align: left;
	color: $lightFonts;
	// color: #707070;
	top: 19px;
	// left: 31px;
	width: 100%;
	padding-left: 31px;
	z-index: 1;
}
input {
	// &::placeholder,
	&::-webkit-input-placeholder,
	&::-moz-placeholder,
	&:-moz-placeholder,
	&:-ms-input-placeholder {
		color: $lightFonts;
	}
	&::-moz-placeholder,
	&:-moz-placeholder {
		color: #000;
	}
}

.form__arr {
	position: absolute;
	border: 10px solid transparent;
	border-bottom: 0;
	right: 20px;
	top: 25px;
	width: auto;
	border-top-color: $basicColor;
}
.form__btn-form {
	margin-top: 10px;
	margin-bottom: 115px;
	height: 50px;
	width: 265px;
}

@media screen and (max-width: 900px) {
	.appointment {
		width: 90vw;
	}
}
@media screen and (max-width: 650px) {
	.appointment {
		width: 90vw;
	}
	.appointment__cont {
		width: 70vw;
	}
}
