.promotion {
	// min-height: 985px;
	// border:solid 1px rgba(214, 214, 214, 0.726);
	position: relative;
}
// удалить  высоту фон блока
.promotion__cardlist{
	top: -124px;

}
.cardList {
	display: flex;
	justify-content: center;
	position: absolute;
	width: 90vw;
	left: 50%;
	margin-left: -45vw;
	transition: transform 0.15s, top 0.15s;
}
.cardlist__card {
	width: 235px;
	min-width: 210px;
	height: 330px;
	border: solid 1px rgb(243, 243, 243);
	background-color: #fff;
	display: flex;
	flex-direction: column;
	justify-content: start;
	padding: 0 2%;
	align-items: center;
	overflow: hidden;
	white-space: pre-line;
	text-align: center;
	cursor: default;
	transition: transform 0.8s ease-in-out, opacity 0.8s ease-in-out;
	z-index: 10;
	box-shadow: 5.3px 7.3px 24.7px 4.4px rgba(109, 109, 109, 0.1);

	&:hover {
		transform: scale(1.2) translateY(5%);

		transition: transform 0.2s ease-in;
		z-index: 11;
	}
	&:hover .card__btn {
		opacity: 1;
	}
}
.card__icon {
	height: 97px;
	margin-top: 14%;
}
.card__head {
	margin-top: 23px;
	max-width: 200px;
	text-transform: uppercase;
	font-family: "Montserrat";
	font-weight: 600;
	font-size: 16px;
}
.card__text {
	max-width: 186px;
	word-wrap: break-word;
	display: inline-block;
	font-family: "Open Sans";
	font-size: 14px;
	line-height: 20px;
}
.card__btn-card.btn {
	height: 30px;
	outline-color: $secondaryColor * 1.4;
}
.card__btn {
	margin-top: auto;
	margin-bottom: 25px;
	opacity: 0;
	font-size: 10px;
}

.promotion__clarific {
	padding-top: 310px;
}
.clarific {
	text-align: center;
	width: 70%;
	// min-width: 500px;
	margin: 0 auto;
}

.clarific__text {
	font-size: 16px;
	margin-bottom: 120px;
}

@media screen and (max-width: 1024px) {
	.promotion__cardlist {
		// transform: scale(0.9);
		top: -95px;
	}
}
@media screen and (max-width: 900px) {
	.promotion__cardlist {
		transform: scale(0.9);
		top: -75px;
	}
}

@media screen and (max-width: 670px) {

	.cardList{
		flex-direction: column;
		align-items: center;
		position: relative;
	}
	.promotion__cardlist{
		top: initial;
	
	}
	.cardlist__card{
		margin-bottom: 30px;
		width: 300px;
		height: 370px;

	}
	.card__head{
		font-size: 20px;
	}
	.card__head {
		font-size: 16px;
	}
	.card__btn {
		margin-bottom: 40px;
	}
	.promotion__clarific {
		padding-top: initial;
	}
	.clarific{
				width: 85%;

	}
	.clarific__text {
		margin-bottom: 60px;
	}
	

}