.about {
	box-sizing: border-box;
	min-height: 100%;
	margin-bottom: 120px;
}
.about__banner {
	width: 100%;
	// height: auto;
	object-fit: cover;
}
.aboutNova__head {
	margin-bottom:60px;
}
.aboutNova{
display: flex;
flex-direction: column;
align-items: center;

}
.aboutNova_desc {
	font-family: "Open Sans";
	font-size: 26px;
	line-height: 1.46;
	letter-spacing: 0.52px;
	text-align: center;
	color: $lightFonts;
	width: 90%;
	margin-bottom: 60px;
}

.certificate{
	margin-top: 150px;
	margin-bottom: 105px;
}
.certificate__list{
	display: flex;
	justify-content: space-around;
}
.certificate__image{
	width: 331px;
	height: 397px;
	border: 1px solid gray;
	border-radius: 2px;
	margin:0 15px;
	object-fit: contain;
}

@media screen and (max-width: 760px){
	.aboutNova_desc{
		font-size: 20px;
		margin-bottom: 40px;
	
	}
	.about{
		margin-bottom: 60px;
	}
	.aboutNova__head {
		margin-bottom: 40px;
	}
	.aboutNova__head{
		margin-top: 60px;
		margin-bottom: 30px;
	}
}