.btn {
	margin-right: 0;
	min-width: 150px;
	max-width: 250px;
	height: 50px;
	border-radius: 25px;
	border: none;
	background-color: $basicColor;
	color: $bgcbasic;
	font-family: "Montserrat";
	font-size: 14px;
	font-weight: 600;
	text-transform: uppercase;
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
	// transition: transform 0.3s ease-in, opacity 0.3s ease-in, fill 0.8;
	transition: transform 0.3s ease-in, opacity 0.3s ease-in,background-color 0.8s;
	cursor: pointer;
	& span {
		display: block;
		transition: transform 0.15s ease-in;
	}
	&:hover {
		transform: scaleX(1.1);
		background-color: $basicColor*1.1;
		transition: transform 0.15s ease-in-out, opacity 0.5s ease-in, background-color 0.15s;
		
	}
	&:hover span{
		transform:none
	}

	&:active {
		background-color: $basicColor * 1.1;
		transform: scaleX(1.05);
		text-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
		transition: transform 0.15s, background-color 0.15s;
	}
}
@media screen and (max-width: 900px) {
	.btn {
		min-width: 130px;
		max-width: 250px;
		height: 37px;
	}
}
