/*----- popup start-----*/

.popupWrap {
	display: flex;
	width: 100%;
	min-height: 100%;
	background-color: #00000066;
	overflow: hidden;
	position: fixed;
	z-index: 999;
	top: 0px;
}

.popup {
	margin: auto;
	background-color: #fff;
	border-radius: 3px;
	box-shadow: 5px 5px 30px 5px #3838384d;
	position: relative;
	width: 60vw;
	padding-bottom: 43.75%;
	font-family: "Open Sans";
}
.popupCont {
	display: flex;
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
}
@media screen and (max-width: 500px) {
	.modal-open {
		overflow: hidden;
		position: fixed;
	}
	.popupWrap {
		background-color: #fff;
	}
	.popup {
		width: 70%;
		max-height: 84vh;
		flex-direction: column;
		margin-top: 19%;
		box-shadow: none;
		/* overflow: auto; */
	}
	.popupCont {
		overflow: auto;
		overflow-x: hidden;
	}
	.desc {
		padding: 8px;
	}
	.popup__photo {
		width: 100%;
		object-fit: contain;
		object-position: center top;
		position: relative; /* height: auto; */
		top: 0;
		border-radius: 3px 0 0 3px;
	}
}
// .popup__desc {
// }

.popup__photo {
	height: 100%;
	border-radius: 3px 0 0 3px;
}
.closeButton {
	position: absolute;
	height: 40px;
	width: 40px;
	// border-radius: 50%;
	top: -20px;
	right: -20px;
	box-sizing: border-box;
	overflow: hidden;
}
.Button {
	height: 100%;
	width: 100%;
	border-radius: 50%;
	fill: $basicColor;
	// top: -20px;
	// right: -20px;
	border: 1px solid rgba(255, 255, 255, 0);
	transition: fill 0.15s, border 0.5s;
	&:hover {
		fill: $basicColor * 1.1;
		cursor: pointer;
	}

	&:active {
		fill: $basicColor * 0.9;
		cursor: pointer;
		border: 1px solid white;
		transition: border 0.15s, color 0.15s;
		color: rgba(255, 255, 255, 0.664);
	}
}
.desc {
	display: flex;
	flex-direction: column;
	justify-content: start;
	overflow: hidden;
	width: 100%;
	margin: 0 auto;
	padding-right: 3%;
	padding-left: 3%;
}
.desc__fullname {
	font-size: 24px;
	text-transform: capitalize;
	margin-top: 18%;
	margin-bottom: 8%;
	font-family: "Montserrat";
}
.desc__position {
	font-style: 18px;
}
.desc__position::after {
	content: "";
	display: block;
	border-bottom: 1px solid #ec407a;
	width: 150px;
	margin-top: 4%;
	margin-bottom: 14%;
}
// .desc__experience {
// 	/* margin-top: 50px; */
// }
.desc__head {
	font-size: 20px;
	margin-top: 8%;
	padding-bottom: 5px;
	margin-bottom: 5px;
}
.desc__spec {
	font-size: 16px;
	margin-top: 5px;
	/* margin-top: 8px; */
}
/*----- popup end -----*/

.popupDoc{
	width: 80vw;
	padding-bottom: 57%;
}