* {
	box-sizing: border-box;
}

.banner {
	position: relative;
	// background-color: yellowgreen;
	height: 56vmin;
	overflow: hidden;

	@media (min-aspect-ratio: 10/8) {
		height: 72vmin;
	}

	@media (min-aspect-ratio: 16/10) {
		height: 90vmin;
	}

	@media (min-aspect-ratio: 2/1) {
		height: 100vmin;
	}
}

.banner__background {
	position: absolute;
}

.banner__frame {
	box-sizing: border-box;
	pointer-events: none;
	transform: scale(1.02);
	background-color: rgb(255, 255, 255);
	width: 100vw;
	height: 56.2vw;
	@media (max-width: 750px) {
		width: 120vw;
		height: 67.2vw;
	}
}

.title .banner__heading {
	// padding-top: 8%;
	padding-top: 7vw;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

.heading {
	font-size: 54px;
	font-weight: 900;
	color: white;
	text-transform: uppercase;
	text-align: center;
}

.heading__first {
	font-weight: 600;
	padding-bottom: 20px;
}
.heading__desc {
	font-size: 22px;
	font-weight: 500;
	line-height: 33px;
	padding-top: 4%;
	padding-bottom: 4%;
	text-transform: none;
	max-width: 75%;
	margin: 0 auto;
	word-wrap: break-word;
}
.heading__btn {
	// margin-top: 4%;
	margin-right: auto;
	margin-left: auto;
}
.btn.heading__btn-banner {
	width: 360px;
	max-width: 360px;
}

@media screen and (max-width: 1280px) {
	// .title .banner__heading {
	// 	// padding-top: 6%;
	// }
}

@media screen and (max-width: 1024px) {
	.heading {
		font-size: 54px * 0.8;
	}
	.heading__desc {
		font-size: 22px * 0.8;
	}
}
@media screen and (max-width: 900px) {
	// .title .banner__heading {
	// 	// padding-top: 6%;
	// }
	.heading__desc {
		padding-top: 2%;
	}
	.heading {
		font-size: 27;
	}
	.heading__desc {
		font-size: 22px * 0.5;
	}
}
@media screen and (max-width: 750px) {
	// .title .banner__heading {
	// 	// padding-top: 6%;
	// }
	.heading__desc {
		padding-top: 2%;
	}
	.heading {
		font-size: 54px * 0.6;
	}
	.heading__desc {
		font-size: 22px * 0.5;
	}
}

@media screen and (max-width: 570px) {
	.heading {
		font-size: 24px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
	.heading__desc {
		font-size: 22px * 0.5;
		max-width: 90%;
	}
	.btn.heading__btn {
		margin-bottom: 4vw;
		min-height: 26px;
		width: 80vw;
	}
}
@media screen and (max-width: 470px) {
	.title .banner__heading {
		padding-top: 4%;
	}
	.heading__desc {
		// padding-top: 3%;
		line-height: 25px;
		display: none;
	}

	.heading__first {
		padding-bottom: 10px;
	}
	.btn.heading__btn {
		margin-top: 25px;
	}
}
