.head-underline {
	margin-top: 120px;
	margin-bottom: 104px;
}
// \img\advantages diagnostic.jpg hospital.jpg consultation.jpg
.enumeration {
	background-color: #d9edf7;
}
.enumeration-left {
	margin-right: auto;
}
.enumeration-right {
	margin-left: auto;
}
.enumeration__point {
	// min-height: 607px;
	width: 50%;
	padding-left: 8vw;
	padding-top: 8vw;
	padding-bottom: 8vw;
}
%bg-image {
	content: "";
	display: block;
	width: 50%;
	min-height: inherit;
	// background-image: url(../img/advantages/consultation.jpg);
	background-size: cover;
	background-position: right top;
	background-repeat: no-repeat;
}
.consultation,
.hospital,
.diagnostic {
	// min-height: 607px;
	display: flex;
}

.consultation {
	&:before {
		@extend %bg-image;
		background-image: url(../img/advantages/consultation.jpg);
	}
}
.hospital {
	&:after {
		@extend %bg-image;
		background-image: url(../img/advantages/hospital.jpg);
		background-position: left top;
	}
}

.diagnostic {
	&:before {
		@extend %bg-image;
		background-image: url(../img/advantages/diagnostic.jpg);
	}
}

.enumeration__head {
	font-family: "Montserrat";
	max-width: 36vw;
	color: $darkFonts;
	font-size: 32px;
	font-weight: 800;
	margin-bottom: 19px;
	width: 70%;
}

.enumeration__text,
.enumeration__item {
	max-width: 36vw;
	font-size: 22px;
	font-weight: 400;
	line-height: 1.5;
	color: $lightFonts;
	font-family: "Open Sans";
}
.enumeration__text {
	margin-top: 19px;
	padding-right: 15px;
	margin-bottom: 3vw;
}
.enumeration__item {
	margin-left: 16px;
	white-space: pre-wrap;
	&:before {
		content: "\e903";
		font-family: "novamed";
		color: #f59fbc;
		position: relative;
		right: 16px;
		top: calc(50% - 11px);
		font-size: 25px;
		font-weight: 400;
	}
}





@media screen and (max-width: 1280px) {
	.enumeration__text,
	.enumeration__item {
		font-size: 18px;
	}
	.enumeration__head {
		font-size: 26px;
	}
}

@media screen and (max-width: 1000px) {
	.enumeration__text,
	.enumeration__item {
		font-size: 15px;
		max-width: 42vw;
	}
	.enumeration__head {
		font-size: 21px;
		max-width: 42vw;
	}
	.enumeration__point {
		padding-left: 5vw;
		padding-top: 5vw;
		padding-bottom: 5vw;
	}.enumeration__text {
		margin-bottom: 2vw;
	}
}
@media screen and (max-width: 750px) {
	.consultation, .diagnostic{
		flex-direction: column;
	}
	.hospital{
		flex-direction: column-reverse;
	}
	.consultation:before, .hospital:after, .diagnostic:before{
		padding-top: 56%;
		width: 100%;
	}
	.enumeration__point{
		width: 100%;
	}
	.enumeration__text, .enumeration__item{
		max-width: initial;
	}
	.head-underline {
		margin-top: 80px;
		margin-bottom: 15px;
	}
	
}