@mixin hoverfocusactive($fill){
	transition: transform 0.5s ease-in-out, fill 0.8s;
	&:hover, &:focus{
		transform: scale(1.1);
		fill:$fill*1.1;
		transition: transform 0.15s ease-in-out, fill 0.3s;
	}
	&:active{
		transform: scale(1.05);
		fill:$fill*1.4;
		transition: transform 0.1s ease-in-out, fill 0.1s;

	}
}

.footer{
	background-color: $basicColor;
	min-height: 175px;
	color: #fff;
	font-family: "Open Sans";
	font-size: 14px;
	text-transform: uppercase;
	font-weight: 400;
	flex: 0 0 auto;


	// position: absolute;
	// left: 0;
	// bottom: 0;
	// width: 100%;
}
.footer__contacts{
	display: flex;
	justify-content: space-between;
	align-items: center;
	line-height: 1.5;
	min-height: 150px;

}
.footer__address, .footer__phone, .footer__social{
	text-align: center;
		margin-left: 0;
	padding-left: 0;
}
.footer__logo{
	fill: #fff;
	width: 213px;
	height: 61px;
	display: block;
	@include hoverfocusactive(white);
}
// .logoNova{

// }
.footer .phone__call{
	&:hover{
		text-decoration: underline;
		color: inherit;
	}
}
