.footer__head{
	font-family: 'Montserrat';
	font-size: 22px;
	font-weight: 600;
	line-height: 1.4;
	margin-bottom: 5px;
	color: inherit;
	text-transform: none;
}
.social__list{
	display: flex;
	position: relative;
	justify-content: space-between;
	top: 10px;
}
.social__link{
	text-decoration: none;
	display: inline;

}

.social__icon{
	// width: 50px;
	padding: 5px;
	height: 40px;
	fill:$secondaryColor;
	transition: transform 0.5s ease-in-out, fill 0.8s;
	@include hoverfocusactive($secondaryColor);
}
.social__icon.quad{
	width: 50px;
}
.social__icon.rec{
	width: 60px;
}
.copyright{
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color:inherit;
  & span{
	  text-transform: none;
  }
}

@media screen and  (max-width: 950px){
	.footer{
		font-size: 12px;
	}
	.footer__head{
		font-size: 18px;
	}
	.footer__logo{
		width: 170px;
		
	}
	.social__icon {
		width: 50px;
		height: 34px;
	}
}

@media screen and (max-width: 760px){
	.footer__linklogo{
		display: none;
	}
	.footer__contacts{
		flex-direction: column;
	}
	.footer__address{
		order: 1;
		margin-top: 30px;
	}
	.footer__phone{
		order:2;
		margin-top: 30px;

	}
	.footer__social{
		order: 3;
		margin-top: 25px;
		margin-bottom: 30px;

	}
	.footer__social .footer__head{
		display: none;
	}
	.copyright{
		padding-bottom: 13px;
	}
}