.services {
	padding-top: 120px;
	padding-bottom: 120px;
}
.serviceList {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 4%;
	margin-bottom: 12%;
}
.services__items {
	box-sizing: border-box;
	display: flex;
	// align-items: center;
    justify-content: center; 
    // align-content: center; 
	min-height: fit-content;
	width: calc((100% / 3) - 40px / 3);
	padding-top: calc(((100% / 3) - 40px / 3) / 1.73);
	margin-top: 1.8%;
	position: relative;
	cursor: pointer;
	transition: width 0.1s, transform 0.5s ease-out, box-shadow 0.5s ease-out;
	border-radius: 2px;
	box-shadow: 1px 1px 6px 3px  rgba(129, 129, 129, 0.2);

	&:hover, &:focus {
		box-shadow: 6px 4px 5px 4px rgba(82, 82, 82, 0.2);
		transform: translate(-2px, -1px);
		transition: transform 0.5s cubic-bezier, box-shadow 0.5s cubic-bezier;
		outline-color: $basicColor;
	}
	&:active{
		box-shadow: 6px 4px 5px 4px rgba(82, 82, 82, 0.2)
		, inset 3px 1px 26px 16px rgba(255, 255, 255, 0.2);
		transform: translate(-3px, -2px);
		transition: box-shadow 0.1s,transform 0.1s;

	}
}

.serviceList__text {
	// display: block;
	// position: absolute;
	// top: 0;
	// right: 0;
	// bottom: 0;
	// left: 0;
	width: 80%;
	// height: 100%;
	text-align: center;
	// margin: auto;
	position: relative;
    bottom: 28%;
	text-transform: uppercase;
	font-family: "Montserrat";
	font-size: 25px;
	font-weight: 900;
	color: #fff;
	word-wrap: normal;
	white-space: pre-line;
}
@mixin serv($link) {
	background-color: $secondaryColor;
	background-image: url($link);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}
.services__items[antenatal] {
	@include serv("../img/services/consyltat.jpg");
}
.services__items[pregnancy] {
	@include serv("../img/services/vedennya.jpg");
}
.services__items[birth] {
	@include serv("../img/services/pology.jpg");
}
.services__items[aesthetics] {
	@include serv("../img/services/estety.jpg");
}
.services__items[gynecologist] {
	@include serv("../img/services/operatuv.jpg");
}
.services__items[other] {
	@include serv("../img/services/inshe.jpg");
}


@media screen and (max-width: 950px) {
	.serviceList__text {
		font-size: 21px;
	}
	.services__items {
		width: calc((100% / 2) - 20px / 2);
		padding-top: calc(((100% / 2) - 20px / 2) / 1.73);
	}
}

@media screen and (max-width: 750px) {
	.services__items {
		width: 100%;
		padding-top: calc(100% / 1.73);
	}

	.serviceList__text {
		font-size: 35px;
	}
}
