@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800,900|Open+Sans:400,600,700,800&display=swap&subset=cyrillic');

@font-face {
	font-family: 'novamed';
	src:  url('../fonts/novamed-v1.0/novamed.eot?89te9j');
	src:  url('../fonts//novamed-v1.0/novamed.eot?89te9j#iefix') format('embedded-opentype'),
	  url('../fonts/novamed-v1.0/novamed.ttf?89te9j') format('truetype'),
	  url('../fonts/novamed-v1.0/novamed.woff?89te9j') format('woff'),
	  url('../fonts/novamed-v1.0/novamed.svg?89te9j#novamed') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'novamed' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
  
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  }
