.personsList {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: flex-start;

	margin-right: -5%;
	margin-left: -5%;
	margin-bottom: 120px;
}
.person__header{
	margin-top: 64px;
}
.person__text {
	width: 80vw;
	font-family: "Open Sans";
	font-size: 22px;
	line-height: 1.5;
	text-align: center;
	color: $lightFonts;
	margin-bottom: 60px;
	margin-left: auto;
	margin-right: auto;
}
.popupHidden {
	visibility: hidden;
	display: none;
}

.cardPerson {
	width: 278px;
	min-height: 306px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-left: 5%;
	margin-right: 5%;
	margin-top: 15px;
	margin-bottom: 15px;
	font-family: "Montserrat";
	font-size: 16px;
	line-height: 30px;
	text-transform: uppercase;
	border: 1px solid rgba(99, 99, 99, 0);
	border-radius: 4px;
	transition: box-shadow 0.5s ease-in, color 0.5s ease-in;
	padding: 3px;
	// list-style: none;
	color: $lightFonts;
}

.cardPerson__photo {
	width: 200px;
	height: 200px;
	border-radius: 50%;
	object-fit: cover;
	object-position: center top;
	margin-right: auto;
	margin-left: auto;
	margin-top: 0;
	filter: grayscale(100%);
	-webkit-filter: grayscale(100%);
	transition: filter 0.5s ease-in-out;
}
.cardPerson:hover .cardPerson__photo {
	filter: grayscale none;
	-webkit-filter: none;
}
.cardPerson:hover {
	cursor: pointer;
	color: $darkFonts;
}
.cardPerson:focus {
	outline: $darkFonts;
}
.person {
	width: 100%;
	height: auto;
	margin-top: 17px;
	margin-bottom: 5px;
}
.person__name,
.person__position,
.person__surename {
	text-align: center;
	font-weight: 700;
	// color: $darkFonts;
}
.person__position {
	font-size: 12px;
	text-transform: none;
	white-space: pre-wrap;
}

.hidden {
	display: none;
	visibility: hidden;
}

@media screen and (max-width: 750px) {
	.cardPerson__photo {
		width: 300px;
		height: 300px;
	}
	.cardPerson {
		font-size: 20px;
		width: auto;
	}
	.person {
		margin-top: 21px;
	}
	.person__position {
		line-height: 1.7;
	}
	.person__header {
		font-size: 20px;
	}
	.person__text {
		font-size: 16px;
		margin-bottom: 30px;
	}
}
