.contacts {
	display: flex;
	justify-content: space-around;
	align-items: center;
	padding-top: 50px;
	padding-bottom: 50px;
	font-size: 16px;
	font-family: "Open Sans";
	font-weight: normal;
}
.contacts__map{
	width: 500px;
	height: 375px;
	// padding-top: 57%;
	border: 1px solid $basicColor;
	box-sizing:content-box;
	box-shadow: 5px 6px 10px 2px #bbbbbb36;
	position: relative;

}
.frameMap {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

// 	&:hover{
// 	}

// 	// background-color:rgb(252, 225, 225);
// }
.contacts__adress {
	width: 30%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-left: 20px;
	line-height: 22px;
	font-size: 18px;
	font-weight: 600;
	color: $darkFonts;

}
.contacts__head {
	font-size: 22px;
	padding-left: 20px;
	font-family: "Montserrat", sans-serif;
	font-weight: 600;
	padding-bottom: 15px;
}
.contacts__item {
	margin-bottom: 30px;
}
.contacts__call {
	text-decoration: none;
	font-weight: 600;
	color: $darkFonts;
	&:hover{
		text-decoration: underline;
	}
}
.contacts__week{
	font-weight: 600;
	color:$basicColor ;
}
.contacts__header{
	margin-bottom: 0;
	padding-bottom: 0;
}

@media screen and (max-width: 800px){
	.contacts{
		flex-direction: column-reverse;
	}
	.contacts__header{
		font-size: 30px;
	}
	.contacts__adress{
		width: 100%;
		flex-direction: row;
		justify-content: space-around;
		flex-wrap: wrap;
		margin-left: initial;
	}
	.contacts__item{
		min-width: 180px;
		// padding-left: ;
		margin-left: 15px;
		margin-right: 15px;
		font-size: 14px;
	}
	.contacts__head{
		font-size: 20px;
		padding-left: 0px;
		padding-bottom: 8px;
	}
	.contacts__map{
		width: 90vw;
		height: initial;
		padding-bottom: 75%;
	}
	
}
