.gallery .serviceList-gallery {
	margin-top: 0;
}
.serviceList__text-gallery {
	position: absolute;
	font-size: 21px;
	width: 95%;
	bottom:32px;
	z-index: 15;
}
.img-gal {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
}
.services__items-gallery {
	padding-top: calc(((100% / 3) - 40px / 3) / 1.2);
	margin-bottom: 110px;
	background-image: none;
}
@mixin gall($link-g) {
	&:after {
		content: "";
		height: 100%;
		width: 100%;
		background-image: url($link-g);
		background-color: #ffffff00;
		background-position: center;
		background-repeat: no-repeat;
		background-size: 90%;
		opacity: 0.5;
		position: absolute;
		top: 19%;
		z-index: 9;
		transition: top 0.5s;
		filter: brightness(30%);
	}
	&:before {
		content: "";
		height: 100%;
		width: 100%;
		background-image: url($link-g);
		background-color: #ffffff00;
		background-position: center;
		background-repeat: no-repeat;
		background-size: 80%;
		opacity: 0.3;
		position: absolute;
		top: 27%;
		z-index: 8;
		transition: top 0.5s;
		filter: brightness(30%);
	}
	.gallery__img {
		background-color: $secondaryColor;
		background-image: url($link-g);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 10;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			z-index: 11;
			background: linear-gradient(
				to bottom,
				rgba(255, 255, 255, 0) 41%,
				rgba(255, 255, 255, 0) 46%,
				rgba(0, 0, 0, 0.56) 90%,
				#000000a8 98%
			);
		}
	}
}

.gallery .services__items-gallery:hover,
.gallery .services__items-gallery:focus {
	&:after {
		top: 22%;
	}
	&:before {
		top: 33%;
	}
}
.services__items-gallery[pregnancy] {
	@include gall("../img/gallery/_pregnancy.jpg");
}
.services__items-gallery[gynecologist] {
	@include gall("../img/gallery/_gynecology.jpg");
}
.services__items-gallery[aesthetics] {
	@include gall("../img/gallery/_aesthetics.jpg");
}

.services__items-gallery[antenatal] {
	@include gall("../img/gallery/_consultation.jpg");
}
.services__items-gallery[birth] {
	@include gall("../img/gallery/_birth.jpg");
}

.services__items-gallery[plastic] {
	@include gall("../img/gallery/_implant.jpg");
}

@media screen and (max-width: 950px) {
	.serviceList__text-gallery {
		font-size: 18px;
		bottom: 20px;
	}
	.services__items-gallery {
		width: calc((100% / 2) - 20px / 2);
		padding-top: calc(((100% / 2) - 20px / 2) / 1.2);
	}
}

@media screen and (max-width: 750px) {
	.services__items-gallery {
		width: 100%;
		padding-top: calc(100% / 1.2);
	}
	.serviceList__text-gallery {
		font-size: 35px;
		bottom: 30px;

	}

}
