@import "_variables";
@import "_fonts";
* {
	margin: 0;
	padding: 0;
	outline-color: $secondaryColor;
}

li {
	list-style: none;
	white-space: nowrap;
}

html,
body {
	scroll-behavior: smooth;
	height: 100%;
}

.wrap {
	margin: 0 auto;
	min-width: 320px;
	min-height: 100%;
	overflow: hidden;
	background-color: $bgcbasic;
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	font-size: 20px;
	color: $darkFonts;
	display: flex;
	flex-direction: column;
}

.container {
	max-width: 1170px;
	min-width: 900px;
	margin: 0 auto;
	padding: 0 15px;
	// border:1px solid rgba(128, 128, 128, 0.329);
}
.main {
	flex: 1 0 auto;
	width: 100%;
}
.head {
	font-size: 42px;
	padding-bottom: 30px;
	font-family: "Montserrat";
	font-weight: 800;
	color: $darkFonts;
	text-align: center;
	text-transform: uppercase;
}
.head-underline {
	margin-top: 120px;
	margin-bottom: 104px;
	&:after {
		content: "";
		display: block;
		border-bottom: 1px solid $basicColor;
		height: 1px;
		width: 15%;
		margin: 0 auto;
		margin-top: 21px;
	}
}

@media screen and (max-width: 950px) {
	.container {
		max-width: 768px;
		min-width: 420px;
	}
}
@media screen and (max-width: 750px) {
	body {
		background-color: rgba(255, 0, 0, 0.541);
	}
	.container {
		max-width: 750px;
		min-width: 320px;
	}
	.wrap {
		// min-width: 730px;
	}
}

