.phone,
.schedule,
.address,
.social {
	font-family: "Open Sans";
	color: inherit;
	position: relative;
	width: fit-content;
	padding-left: 51px;
	margin-left: 5%;
	white-space: nowrap;
	word-wrap: break-word;
}
.phone__call {
	text-decoration: none;
	color: inherit;
	transition: transform 0.8s, text-shadow0.8s;
	&:hover {
		transform: scale(1.1);
		// font-weight: 700;
		text-shadow: 0 0 3px rgba(109, 109, 109, 0.493);
		color: $lightFonts * 0.9;
		transition: transform 0.3s, text-shadow 0.5s;
	}
}